import './hero.css';
import title from '../assets/title.svg';
import earth from '../assets/earth.svg';
import sparkles from '../assets/sparkles.svg';
import stars from '../assets/stars.svg';

function Hero() {
  return (
    <div className="hero-container" style={{height: window.innerHeight}}>
        <img src={title} className="title-image" alt="title" style={{top: window.innerHeight * 0.1}}></img>
        <img src={sparkles} className="sparkles-image" alt="sparkles" style={{top: window.innerHeight * 0.28}}></img>
        <img src={earth} className="earth-image" alt="earth" style={{top: window.innerHeight * 0.4}}></img>
        <img src={stars} className="stars-image" alt="stars" style={{top: window.innerHeight * 0.22}}></img>
    </div>
  );
}

export default Hero;
