import './hero-clouds.css';
import bigCloud from '../assets/clouds_1.svg';
import smallCloud from '../assets/clouds_2.svg';

function HeroClouds() {
    return (
        <div className="hero-cloud-container" style={{height: window.innerHeight}}>
            <img src={bigCloud} className="cloud-1 big-cloud static-cloud" alt="cloud_1"></img>
            <img src={smallCloud} className="cloud-2 small-cloud static-cloud" alt="cloud_2"></img>
            <img src={bigCloud} className="cloud-3 big-cloud static-cloud" alt="cloud_3"></img>
            <img src={bigCloud} className="cloud-4 big-cloud static-cloud" alt="cloud_4"></img>
            <img src={smallCloud} className="cloud-5 small-cloud static-cloud" alt="cloud_5"></img>
            <img src={bigCloud} className="cloud-6 big-cloud static-cloud" alt="cloud_6"></img>
        </div>
    )
}

export default HeroClouds;